.body {
  margin: 0;
  font-family: "OpenSans", "Arial", sans-serif; }

.header {
  display: grid;
  grid-template-columns: 16px 68px 1fr 34px 16px;
  grid-template-rows: 50px 70px -webkit-max-content 70px 70px auto;
  grid-template-rows: 50px 70px max-content 70px 70px auto;
  background-color: #ffffff; }
  @media (min-width: 768px) {
    .header {
      grid-template-rows: 50px 84px -webkit-max-content 80px 70px auto;
      grid-template-rows: 50px 84px max-content 80px 70px auto; } }
  @media (min-width: 880px) {
    .header {
      grid-template-columns: 1fr 393px 160px 327px 1fr;
      grid-template-rows: 62px 256px 21px -webkit-max-content 240px;
      grid-template-rows: 62px 256px 21px max-content 240px; } }
  .header::before {
    width: 100%;
    height: 390px;
    content: "";
    background-image: url("../img/bg-bike-320.jpg");
    background-repeat: no-repeat;
    background-position: 50% 0;
    background-size: 100% auto;
    grid-column: 1 / -1;
    grid-row: 1 / -2; }
    @media (min-width: 768px) {
      .header::before {
        height: 340px;
        background-image: url("../img/bg-bike-768.jpg");
        background-repeat: no-repeat;
        background-position: 50% 0;
        background-size: 100% auto; } }
    @media (min-width: 880px) {
      .header::before {
        height: 825px;
        background-image: url("../img/bg-bike-big.jpg");
        background-repeat: no-repeat;
        background-position: 50% 0;
        background-size: 1440px 825px;
        grid-row: 1 / -1; } }
    @media (min-width: 1440px) {
      .header::before {
        background-size: 100% auto;
        background-position: 50% 100%; } }

.header__logo-link {
  margin-top: auto;
  display: inline-block;
  grid-column: 2 / 3;
  grid-row: 1 / 2; }
  @media (min-width: 880px) {
    .header__logo-link {
      grid-column: 2 / 3;
      grid-row: 3 / 4; } }

.header__logo-svg {
  width: 68px;
  height: 21px; }

.header__menu-link {
  width: 50px;
  height: 50px;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  background: #0ad9c6;
  grid-column: -3 / -1;
  grid-row: 1 / 2; }
  .header__menu-link::before {
    width: 24px;
    height: 14px;
    content: "";
    display: block;
    background-image: url("../img/icon-menu.svg");
    background-repeat: no-repeat;
    background-position: 50% 50%;
    background-size: cover; }
  @media (min-width: 880px) {
    .header__menu-link {
      display: none; } }

.header__nav {
  display: none; }
  .header__nav.active {
    display: block; }
  @media (min-width: 880px) {
    .header__nav {
      display: block;
      grid-column: 2 / -2;
      grid-row: 1 / 2; } }

@media (min-width: 880px) {
  .header__menu-close {
